@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.App {
    text-align: center;
    padding: 2% 5%;
}

.App * {
    box-sizing: border-box;
}

.search-bars {
    box-sizing: border-box;
    border: 1px solid #ccc;
    padding: 10px 10px 35px;
    margin-bottom: 10px;
}

ul {
    padding: 0;
    list-style: none;
}

p {
    margin: 5px 0;
}

input[type="text"],
input[type="number"] {
    padding: 8px 6px 8px 10px;
    border-radius: 4px;
    outline: none;
    border: 1px solid #ccc;
    font-size: 16px;
    line-height: 20px;
    align-self: center;
    height: 38px;
    margin-bottom: 20px;
}

.city {
    width: 270px;
}

#city-search {
    display: flex;
    flex-direction: column;
    margin: auto;
    position: relative;
}

.suggestions {
    width: 250px;
    position: absolute;
    top: 45px;
    z-index: 2;
    align-self: center;
    margin: 0;
    font-size: 14px;
    box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
    text-align: left;
    padding: 8px 6px 8px 10px;
    background-color: #eee;
    cursor: default;
}

.suggestions > li:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
    background-color: #a8ebe8;
}

.see-ctys-btn:hover {
    background-color: #05d7cc;
    border:#eee;
    border-radius: 4px;

}

.event {
    padding: 10px 10px 35px;
    margin-bottom: 10px;
    text-align: left;
    border: 1px solid #ccc;
    border-radius: 4px;
    position: relative;
}

.show-details-btn {
    position: absolute;
    right: 5px;
    bottom: 5px;
    padding: 5px 8px;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
}

.show-details-btn:hover {
    background-color: #a8ebe8;
}

.alerts-container {
    position: fixed;
    top: 0px;
    left: 20px;
    width: 250px;
    z-index: 1000;
}

 @media (max-width: 768px) {
    .charts-container{
        display:flex;
        flex-direction: column;
        grid-template-columns: none;
    }
 }

 .charts-container {
    display:flex;
    grid-template-columns: 1fr 1fr;
 }